.App {
  text-align: center;
  align-items: center;
  height: auto;
  width: 70vh;
  margin-top: 10px;
  margin-bottom: 10px;
}

.App-logo {

  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-answer {
  background-color: blue;
  font-size: calc(10px + 2vmin);
  color: black;
  align-items: center;
  text-align: left;
  width: 70vh;
}
.App-header {
  background-color: #282c34;
  height: 50vh;
  width: 70vh;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  color: white;
  align-items: left;
}

.App-link {
  color: #61dafb;
}

.App-refresh {
  background-color: white;
  font-size: calc(10px + 2vmin);
    align-items: left;
    width: 70vh;

}
.App-refresh-button {
  margin-top: 10px;
  margin-bottom: 10px;
  background-color: grey;
  font-size: calc(10px + 2vmin);
  align-items: left;
}

